@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 0;
  margin: 0;
  background-color: #242424;
  z-index: -10;
  position: absolute;
  top: 0px;
  left: 0px;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif;
}

.container {
  margin: 0;
  padding: 0;
  text-align: center;
  width: 100%;
}

.moviePoster {
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 15px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  box-shadow: 0 0 50px #0a0a0a;
}
.moviePoster:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.titleBar {
  width: 100vw;
  background-color: black;
  color: white;
  display: flex;
  align-items: center;
}

.lefttitle {
  padding-left: 7vw;
  display: flex;
  width: 50vw;
  align-items: center;
  justify-content: flex-start;
}

.righttitle {
  width: 50vw;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 7vw;
}

.mainIcon {
  margin: 20px;
  height: 50px;
}

.movieDBlogo {
  height: 60px;
  margin: 20px;
}
input:focus,
textarea:focus {
  outline: none;
}

.inputSearch {
  font-size: 18px;
  display: block;
  width: 40%;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-left: 20px;
  border-radius: 30px;
}

.posterInfo {
  opacity: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
  color: #fff;
  padding: 15px;
  transform: translate(-50%, -50%);
  transition: all 0.4s ease-in-out 0s;
  text-align: center;
  font-size: 18px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -moz-transition: all 0.4s ease-in-out 0s;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -ms-transition: all 0.4s ease-in-out 0s;
}

.posterInfo:hover {
  opacity: 1;
}

.posterInfo h3 {
  width: 80%;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 120px;
}
p {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 90px;
}

.posterButton {
  width: 50%;
  height: 40px;
  border-radius: 30px;
  background-color: rgb(18, 124, 18);
  color: white;
  font-size: 1em;
  font-weight: bold;
  border: none;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  bottom: 50px;
}

.posterButton:hover {
  background-color: rgb(1, 95, 1);
  cursor: pointer;
}

.footer {
  background-color: black;
  color: white;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: italic;
  font-size: 13px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.searchIcon {
  position: relative;
  left: -40px;
  z-index: 5;
  cursor: pointer;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-width: 800px) {
  .titleBar {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .movieDBlogo {
    display: none;
  }

  .lefttitle,
  .righttitle {
    display: flex;
    width: 98%;
    align-items: center;
    justify-content: center;
    padding-left: 0px;
  }
  .inputSearch {
    font-size: 18px;
    display: block;
    width: 60%;
    /* max-width: 300px; */
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 30px;
    border-radius: 30px;
    margin-bottom: 30px;
    margin-left: auto;
    margin-right: auto;
  }
  .titleBar {
    width: 100vw;
  }
  .mainIcon {
    margin-left: 0px;
  }
  .searchIcon {
    position: relative;
    left: -22%;
    bottom: 15px;
    z-index: 5;
  }
}

